
html,body {
  background: #faf8f0;
}
* {
  margin: 0; 
  padding: 0;
}
ul,ol {
  list-style: none;
}
a {
  text-decoration: none;
}
.score,h1 {
  width: 503px;
  margin: 0 auto;
  font-size: 30px;
  font-weight: bold;
}
h1 {
  margin: 20px auto 10px auto;
}

.board {
  width: 503px;
  height: 503px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 10px auto 10px auto;
  border-collapse: separate;
  border-spacing: 10px;
  text-align: center;
  font-weight: bold;
  font-size: 50px;
  color: #fff;
  background: #b9ada1;
  border-radius: 5px;
}
.board td {
    padding: 0;
    height: 25%;
    width: 25%;
    background: #cac0b5;
    border-radius: 5px;
}

